/* styles/Experiences.scss */
@import 'variables';

#experiencia{
    a{
        color: $cor-secundaria;
        font-size: clamp(12px, 2.2vw, 2.2vw ) ;
    }

    /* Center h3 and p.cargo on mobile */
    h3 {
        @media screen and (max-width: 768px) {
            text-align: center;
        }

        @media screen and (max-width: 480px) {
            text-align: center;
        }
    }
    
    .cargo{
        color: $cor-secundaria;
        font-size: clamp(10px, 1.8vw, 1.8vw);

        @media screen and (max-width: 768px) {
            text-align: center;
        }

        @media screen and (max-width: 480px) {
            text-align: center;
        }
    }

    p:not(.cargo){
        font-size: clamp(10px, 1.5vw, 1.5vw);
        padding: 1.5vw 2vw;
        line-height: 1.8;
        margin-bottom: 1.5vw;
    }
}   